






import Vue from 'vue';
import SurveyContainer from './components/SurveyContainer.vue';

import axios from 'axios';

export default Vue.extend({
	components: {
		SurveyContainer
	},
	data() {
		return {
			test: 'AWe'
		};
	},
	mounted() {
		axios
			.get('http://127.0.0.1:5000/questionnaire/question1')
			.then(response => {
				// handle success
				this.test = response.data;
			})
			.catch(function(error) {
				// handle error
				console.log(error);
			})
			.then(function() {
				// always executed
			});
	}
});
