

























import RangeQuestion from '@/models/questions/RangeQuestion';
import Vue from 'vue';
export default Vue.extend({
	props: {
		question: {
			type: RangeQuestion,
			default: (): RangeQuestion => new RangeQuestion(-1, 'Range Question')
		}
	},
	computed: {
		buttonWidth(): number {
			if (this.$vuetify.breakpoint.name == 'xs') {
				return 28;
			}

			return 48;
		}
	}
});
